function recurr(d, num) {
    if (d.length === 0) {
        return;
    } else {
        for (let i = 0; i < d.length; i++) {
            recurr(d[i]["content"], num + 1);

            if (d[i]["content"].length !== 0) {
                d[i]["content"] = {
                    content: (
                        <div>
                            <p>
                                {d[i]["body"]}
                            </p>
                            <Accordion.Accordion panels={d[i]["content"]}/>
                        </div>
                    )
                };
            } else {
                console.log(d[i]["body"])
                d[i]["content"] = {
                    content: (
                        <div>
                            <p>
                                {d[i]["body"]}
                            </p>
                        </div>
                    )
                };
            }
        }
    }
}

var t0 = performance.now();
recurr(d, 0);
var t1 = performance.now();
console.log("Call to accordify took " + (t1 - t0) + " milliseconds.");

const AccordionExampleNested = () => (
    <Accordion defaultActiveIndex={0} panels={d} styled/>
);

export default AccordionExampleNested;
