import React from 'react';
import { graphql } from 'gatsby';
import PropTypes from 'prop-types';
import { Layout } from '@components';
import styled from 'styled-components';
import PdrSec from '../../../emiel-bos/src/components/accordion';

const styleLink = typeof document !== 'undefined' ? document.createElement('link') : null
styleLink.rel = 'stylesheet';
styleLink.href =
  'https://cdn.jsdelivr.net/npm/semantic-ui/dist/semantic.min.css';
typeof document !== 'undefined' ? document.head.appendChild(styleLink) : null

const StyledMainContainer = styled.main`
  counter-reset: section;
`;



const PdrPage = ({ location }) => (
  <Layout location={location}>
    <StyledMainContainer>
      <h1>Promotion justification</h1>
      <h2>Personal development review - 11/07/2022</h2>
      <br />
      <p>
        This is a temporary page made to give an overview of the correlation of NCC's role requirements
        versus acquired skills and already performing responsibilities. <br />
        The requirements have been taken from documents found on sharepoint.
        The Research Engineer (NCCUK-264147561-161) and Advanced RE (N/A) document have been used respectively.
      </p>
      <br />
      <PdrSec />
    </StyledMainContainer>
  </Layout>
);
PdrPage.propTypes = {
  location: PropTypes.object.isRequired,
  data: PropTypes.object.isRequired,
};

export default PdrPage;

export const pageQuery = graphql`
  {
    allMarkdownRemark(
      filter: { fileAbsolutePath: { regex: "/projects/" } }
      sort: { fields: [frontmatter___date], order: DESC }
    ) {
      edges {
        node {
          frontmatter {
            date
            title
            tech
            github
            external
            ios
            android
            company
          }
          html
        }
      }
    }
  }
`;
